import React from "react";
import {connect} from "react-redux";
import PropTypes from "prop-types";

export default class ProductGalleryMiniPicturesComponent extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.loadCarousel();
    }

    componentWillUnmount() {
        this.killModules();
    }

    componentWillUpdate() {
        this.killModules();
    }

    killModules() {
        OPL.UI.stopModules(document.getElementById("mini-carousel"));
    }

    componentDidUpdate() {
        this.loadCarousel();
    }

    loadCarousel() {
        OPL.UI.loadModules(this.ref, [{
            path: 'common/modules/opl-carousel',
            options: {
                asNavFor: ".opl-sticked-element .opl-wiking-gallery-single",
                slidesToShow: 5,
                arrows: true,
                slide: ".opl-wiking-gallery-navigation--item",
                dots: false,
                vertical: true,
                verticalSwiping: true,
                focusOnSelect: true
            }
        }]);
    }

    render() {
        return (
            <div id="mini-carousel">
                <div data-js-module="common/modules/opl-carousel"
                     className="opl-wiking-gallery-navigation opl-carousel-vertical-arrows u-w-100 u-spacing-top"
                     ref={ref => this.ref = ref}>
                    {this.props.productVariant.mediaContent.map((element, index) => <MiniCarouselItem url={element.mediaImageUrl} alt={element.altText} key={index}/>)}
                </div>
            </div>
        )
    }

//


}
const MiniCarouselItem = (props) => {
    return (
        <div className={"opl-wiking-gallery-navigation--item "}>
            <div
                className="opl-feature-object opl-feature-object--zoom-in-zoom-out  opl-feature-object--wiking u-no-padding">
                <div className="opl-feature-object__content">
                    <div className="opl-feature-object__inner-wrapper">
                        <div className="opl-feature-object__inner">
                            <img src={props.url} alt={props.alt}/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )

}

